.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    background-color: black;
}

.home_row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 4px;
}

.home_image{
    width: 100%;
    z-index: -1;
    margin-bottom: -500px;
    mask-image:linear-gradient(to bottom,rgba(0,0,0,1), rgba(0,0,0,0));
}